.banner {
	height: 100%;
	width: 100%;
	position: absolute;
	top: 0;
	left: 0;
	z-index: -1;
	&.projects {
		position: relative;
		width: 100%;
		height: 100%;
		overflow: hidden;
	}

	&__content {
		position: absolute;
		bottom: 3.5rem;
		width: 30%;
		background: $orange;
		padding: 2.5rem 3rem;

		&.projects {
			background: 0 0;
			padding: 0;
			width: auto;
			bottom: 5vh;
			left: 10.5%;

			@media only screen and (max-width: 1366px) {
				left: 9%;
			}

			@media screen and (max-width: 768px) {
				left: 4%;
			}

		}

		@media only screen and (max-width: 1366px) {
			width: 40%;
		}

		@media screen and (max-width: 768px) {
			width: 90%;
			left: 5%;
			padding: 1rem 2.5rem;
		}

		p {
			color: $white;
			font-family: $roboto_regular;
			font-size: 1.4rem;
			@media only screen and (max-width: 1366px) {
				font-size: 1.4rem;
			}
		}

	}

	&__image {
		width: 100%;
		height: 100%;
		background-position: center center;
		background-size: cover;
		background-repeat: no-repeat;
		img {
			opacity: 0;
			visibility: hidden;
		}
	}
}

.video__background {
	&--wrapper {
		height: 100%;
		width: 100%;
		&::after {
			content: '';
			position: absolute;
			top: 0;
			left: 0;
			background-color: rgba(0, 0, 0, 0.5);
			height: 100%;
			width: 100%;
		}
	}
}

.photo__background {
	&--wrapper {
		height: 100%;
		width: 100%;
		background-size: cover;
		background-position: center center;
		background-repeat: no-repeat;
	}
}

.banner__imagebox {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	z-index: -1;
}