.not-found {
	&__container {
		position: relative;
		position: absolute;
		top: 0;
		left: 0;
		height: 100%;
		width: 100%;
		@media screen and (max-width: 768px) {
			top: -10%;
		}
	}
	&__image {
		height: 100%;
		width: 100%;
		background-size: contain;
		background-position: center center;
		background-repeat: no-repeat;
		img {
			visibility: hidden;
			opacity: 0;
		}
	}
}