.process {
	&__container {
		margin-top: 5rem;
	}
	&__hero {
		padding: 5rem 0;
		&--mobile {
			h1 {
				margin: 0 0 2rem;
			}
		}
	}
	&__steps {
		margin: 5rem auto;
		&--content {
			margin: 0 0 3rem;
		}
	}
	&__content {
		&--desc {
			padding: 5rem;
			z-index: 1;
			position: relative;
			h2 {
				margin: 0 0 1.5rem;
			}
		}
		&--number {
			position: absolute;
			top: -11rem;
			left: -5rem;
			font-size: 15rem;
			z-index: -1;
			color: $orange;
			opacity: 0.3;
		}
		&--item {
			opacity: 0;
			visibility: hidden;
			position: absolute;
			width: 40rem;
			top: 4%;
			&.is-active {
				opacity: 1;
				visibility: visible;
				@include transition (opacity 0.5s ease-in-out);
			}
			&.sticky {
				position: fixed;
				top: auto;
			}
		}
	}
	&__image {
		&--item {
			position: relative;
			margin: 5rem 0;
			&:first-child {
				margin: 0 0 5rem;
			}
			img {
				width: 100%;
				height: auto;
			}
		}
	}
}

.process__content-mobile {
	&--image {
		margin: 0 0 2rem;
		position: relative;
		img {
			width: 100%;
			height: auto;
		}
	}
	&--content {
		margin: 0 0 3rem;
		position: relative;
	}
	&--number {
		position: absolute;
		bottom: -50%;
		left: 0;
		font-size: 15rem;
		z-index: -1;
		color: $orange;
		opacity: 0.3;
	}
}

.overlay__image {
	background-color: $orange;
	opacity: 0.7;
	height: 100%;
	width: 100%;
	position: absolute;
	top: 0;
	left: 0;
}