.footer {
	position: relative;
	background-color: $orange;
	padding: 3.5rem 0;
	z-index: 5;
	opacity: 1;
	visibility: visible;
	@media screen and (max-width: 768px) {
		padding: 3.5rem 0 0;
	}
	.people & {
		opacity: 0;
		visibility: hidden;
		z-index: -1;
	}
	.home &, .homepage & {
		opacity: 1;
		visibility: visible;
		position: absolute;
		bottom: 0;
		width: 100%;
		background-color: transparent;
	}
	.connect & {
		opacity: 1;
		visibility: visible;
		position: absolute;
		bottom: 0;
		width: 100%;
		@media screen and (max-width: 768px) {
			position: relative;
		}
	}
	.error404 & {
		opacity: 1;
		visibility: visible;
		position: absolute;
		bottom: 0;
		width: 100%;
		@media screen and (max-width: 768px) {
			position: absolute;
		}
	}
	.nav-open & {
		opacity: 1;
		visibility: visible;
		position: fixed;
		bottom: 0;
		width: 100%;
		z-index: 2;
		@include transition(opacity 1s ease-in);
	}
	&__item {

		&.mobile-view {
			display: none;
		}

		&--phone {
			@media screen and (max-width: 768px) {
				margin-bottom: 2rem !important;
			}
		}

		&--socials {
			font-size: 0;
			@media screen and (max-width: 768px) {
				text-align: left !important;
				margin: 0 0 2rem !important;
			}
			li {
				display: inline-block;
				margin: 0 0.5rem;
				@media screen and (max-width: 768px) {
					margin: 0;
				}
			}
			img {
				max-width: 2rem;
			}
			span {
				line-height: 2.5;
				font-family: $roboto_regular !important;
			}
		}

		li {
			a {
				color: $white;
				font-size: 1.2rem;
				font-family: $roboto_bold;
				&:hover {
					text-decoration: underline;
					opacity: 1;
				}
			}
		}

		span {
			font-size: 1.2rem;
			color: $white;
			font-family: $roboto_bold;
			.connect & {
				color: $white;
			}
		}

		@media screen and (max-width: 768px) {
			text-align: left;
			margin: 0 0 1rem;

			li {
				text-align: left;
			}

			&.mobile-view {
				display: block;
			}

			&--address {
				display: none;
			}

		}

		&--newsletter {
			.wpcf7-form {
				font-size: 0;
				@media screen and (max-width: 768px) {
					text-align: center;
				}
			}
			.wpcf7-form-control-wrap.emailaddress {
				width:70%;
				display: inline-block;
				@media screen and (max-width: 768px) {
					width: 100%;
				}
			}
			.wpcf7-text {
				font-size: 1.2rem;
				color: $white;
				border-bottom-color: $white;
				font-family: $roboto_bold;
				@media screen and (max-width: 768px) {
					font-size: 1rem;
					font-family: $roboto_regular;
				}
				&:focus {
					outline: 0;
				}
				&::-webkit-input-placeholder {
					color: $white;
					font-size: 1.2rem;
					@media screen and (max-width: 768px) {
						font-size: 1.1rem;
						font-family: $roboto_regular;
					}
				}
				&::-moz-placeholder {
					color: $white;
					font-size: 1.2rem;
					@media screen and (max-width: 768px) {
						font-size: 1.1rem;
						font-family: $roboto_regular;
					}
				}
				&:-ms-input-placeholder {
					color: $white;
					font-size: 1.2rem;
					@media screen and (max-width: 768px) {
						font-size: 1.1rem;
						font-family: $roboto_regular;
					}
				}
				&:-moz-placeholder {
					color: $white;
					font-size: 1.2rem;
					@media screen and (max-width: 768px) {
						font-size: 1.1rem;
						font-family: $roboto_regular;
					}
				}
				&.wpcf7-not-valid {
					border-bottom-color: red;
				}
			}
			.wpcf7-submit {
				background-color: $white;
				color: $orange;
				width: calc(20% - 1rem);
				display: inline-block;
				font-size: 1.2rem;
				padding: 0.6rem 0;
				margin-left: 1rem;
				margin-top: 0;
				font-family: $roboto_bold;
				color: #000;
				&:hover {
					background-color: $white;
					color: $orange;
				}
				@media screen and (max-width: 768px) {
					margin: 1rem 0 0;
				}
			}
			.wpcf7-response-output {
				display: none;
			}
			.wpcf7-not-valid-tip {
				display: none;
			}
		}

	}

	&__scripts {
		.wpcf7-email {
			&:focus::-webkit-input-placeholder  {color:transparent;}
			&:focus::-moz-placeholder   {color:transparent;}
			&:-moz-placeholder   {color:transparent;}
		}
	}
}

.privacy__policy--container {
	display: none;
	position: fixed;
	bottom: 0;
	left: 0;
	background: $white;
	width: 100%;
	z-index: 10;
	text-align: center;
	padding: 3rem 0;
	p {
		color: $orange;
		font-size: 1.24rem;
		a {
			font-size: 1.2rem;
			color: $orange;
			font-family: $roboto_bold;
			&:hover {
				text-decoration: underline;
				opacity: 1;
				color: $orange;
			}
		}
	}
	.button {
		display: inline-block;
		padding: 1rem 3rem;
		font-size: 1.2rem;
		margin: 0;
		&:hover {
			opacity: 1;
			background-color: $orange;
			color: $white;
		}
	}
}