.header {
	z-index: 1;
	&__navigation {
		position: relative;
		padding: 3.5rem 0 0;
		z-index: 3;

		a {
			cursor: pointer;
			&:hover {
				opacity: 1;
			}
			.nav-open & {
				opacity: 0;
			}
			img {
				cursor: pointer;
			}
		}

		@media screen and (max-width: 768px) {
			padding: 2rem 0 0;
		}

	}

	&__menu {
		opacity: 0;
		visibility: hidden;
		// position: absolute;
		top: 0;
		z-index: 2;
		right: 0;
		width: 100%;
		height: 100%;
		padding: 0;
		background-color: $orange;
		@include transition(all 0.5s ease);

		.nav-open & {
			position: fixed;
		}

		@media screen and (max-width: 768px) {
			padding: 0;
			left: 0;
			right: auto;
		}

		.home &, .homepage & {
			opacity: 1;
			visibility: visible;
			background-color: transparent;
		}

		.nav-open & {
			opacity: 1;
			visibility: visible;
		}

		&--content {
			position: relative;
			text-align: right;
			height: 100%;

			@media screen and (max-width: 768px) {
				text-align: center;
			}
		}

		&--items {
			// position: absolute;
			// top: 45%;
			// left: 6.9%;
			text-align: left;
			// @include transform(translateY(-50%));
			position: absolute;
			text-align: left;
			top: 0;
			left: 0;
			padding: 15vh 0 0;

			.nav-open & {
				left: 11%;
				top: 10%;
				@media only screen and (max-width: 1440px) and (min-width: 1367px) {
					left: 6%
				}
				@media only screen and (max-width: 1366px) {
					left: 10%;
				}
				@media only screen and (max-width: 1280px) {
					left: 6%;
				}
				@media screen and (max-width: 768px) {
					left: 4%;
				}
				
			}

			@media only screen and (max-width: 1366px) {
				padding: 10vh 0 0;
				// top: 50%;
				// left: 4.5%;
				// text-align: left;
				// .nav-open & {
				// 	left: 4.9%;
				// 	text-align: left;
				// }
			}

			ul li {
				@media screen and (max-width: 768px) {
					text-align: left;
				}
			}

			@media screen and (max-width: 768px) {
				top: 30%;
				left: 0;
				right: auto;
				text-align: left;
				padding: 3vh 0 0;
			}

			a {
				font-family: $roboto_bold;
				font-size: 6rem;
				font-weight: 700;
				line-height: 1.3;
				color: $white;
				@include transition(all 0.5s ease);

				@media screen and (max-width: 768px) {
					font-size: 5rem;
				}

				&:hover {
					color: transparent;
					opacity: 1;
					-webkit-text-stroke-width: 0.2rem;
					-webkit-text-stroke-color: $white;
				}
			}
		}
	}
}

.hamburger {
	font: inherit;
	display: inline-block;
	overflow: visible;
	margin: 0;
	cursor: pointer;
	transition-timing-function: linear;
	transition-duration: 0.15s;
	transition-property: opacity,filter;
	text-transform: none;
	color: inherit;
	border: 0;
	background-color: transparent;

	.home &, .homepage & {
		display: none;
	}

	&.is-active {
		position: relative;
		z-index: 5;
	}

	.hamburger-box {
		position: relative;
		display: inline-block;
		width: 4rem;
		height: 2.4rem;
	}

	.hamburger-inner, .hamburger-inner:after, .hamburger-inner:before {
		position: absolute;
		width: 4rem;
		height: 0.4rem;
		transition-timing-function: ease;
		transition-duration: .15s;
		transition-property: transform;
		border-radius: 4px;
		background-color: $orange;
	}
	.hamburger--collapse {
		.hamburger-inner {
			&::after {
				top: -2rem;
				@include transition(top .2s cubic-bezier(.33333,.66667,.66667,1) .2s,opacity .1s linear);
			}
		}
	}

	.hamburger-inner {
		top: 50%;
		bottom: 0;
		@include transform(translateY(-50%));
		transition-delay: 0.13s;
		transition-timing-function: cubic-bezier(0.55, 0.055, 0.675, 0.19);
		transition-duration: 0.13s;
		&::after, &::before {
			display: block;
			content: "";
		}
		&::before {
			top: -1rem;
			@include transition(top .12s cubic-bezier(.33333,.66667,.66667,1) .2s,transform .13s cubic-bezier(.55,.055,.675,.19));
		}
		&::after {
			bottom: -1rem;
		}
	}
	&.is-active {
		.hamburger-inner {
			transition-delay: .22s;
			transition-timing-function: cubic-bezier(.215,.61,.355,1);
			transform: translate3d(0,-10px,0) rotate(-45deg);
			&::after {
				top: 1rem;
				transition: top .2s cubic-bezier(.33333,0,.66667,.33333),opacity .1s linear .22s;
				opacity: 1;
				background-color: $white;
			}
			&::before {
				top: 1rem;
				transition: top .1s cubic-bezier(.33333,0,.66667,.33333) .16s,transform .13s cubic-bezier(.215,.61,.355,1) .25s;
				transform: rotate(-90deg);
				background-color: $white;
			}
		}
	}
}

body {
	position: relative;
	&.nav-open {
		overflow-x: hidden;
	}
}