html,
body {
	font-size: 62.5%;
	width: 100%;
	height: 100%;
}

.ie7 img {
	-ms-interpolation-mode: bicubic;
}

input,
select,
textarea,
button {
	border: 0 none;
	border-radius: 0;
	background-color: transparent;
	padding: 0;
	margin: 0;
	@include appearance(none);
}

input {
	&::-webkit-input-placeholder {
		color: $gray;
		font-size: 1.6rem;
		opacity: 1;
	}
	&::-moz-placeholder {
		color: $gray;
		font-size: 1.6rem;
		opacity: 1;
	}
	&:-ms-input-placeholder {
		color: $gray;
		font-size: 1.6rem;
		opacity: 1;
	}
	&:-moz-placeholder {
		color: $gray;
		font-size: 1.6rem;
		opacity: 1;
	}
}

select,
textarea {
	color: #000;
	font-size: 16px;
}

ul {
	padding: 0;
	margin: 0;

	li {
		font-size: 15px;
		line-height: 1;
		padding-left: 17px;
		margin-bottom: 10px;
	}
}

ol {
	li {
		font-size: 15px;
		line-height: 1;
		margin-bottom: 20px;

		&:last-child {
			margin-bottom: 0;
		}
	}
}

p {
	font-size: 16px;
	line-height: 1.8;
	color: #161616;
	&:last-child {
		margin-bottom: 0;
	}
}

// Float Definitions

.clear {
	clear: both;
}

.right {
	float: right;
}

.left {
	float: left;
}

.alignright {
	float: right;
	margin: 0 0 2px 7px;
}

.alignleft {
	float: left;
	margin: 0 7px 2px 0;
}

img {
	&.left,
	&.alignleft {
		float: left;
		margin: 0 15px 7px 0;
	}
	&.right,
	&.alignright {
		float: right;
		margin: 0 0 7px 15px;
	}
}

.post img.aligncenter,
.attachment img.aligncenter {
	display: block;
	text-align: center;
	margin: 0 auto;
}

.nopad {
	padding: 0px !important;
}

.nomargin {
	margin: 0px !important;
}

.noborder {
	border: none !important;
}

.grid-container {
	max-width: 150rem;
	padding: 0;

	@media only screen and (max-width: 1440px) and (min-width: 1367px) {
		max-width: 125rem;
	}

	@media only screen and (max-width: 1366px) {
		max-width: 110rem;
	}

	@include breakpoint(medium only) {
		max-width: 90%;
	}

	@include breakpoint(small only) {
		max-width: 90%;
	}
}

h1 {
	font-family: $roboto_bold;
	font-size: 4.9rem;
	color: $orange;
	line-height: 1.2;
	padding: 0;
	margin: 0;

	@include breakpoint(small only) {
		font-size: 3rem;
	}
}

h2 {
	font-family: $roboto_bold;
	font-size: 3.5rem;
	color: $orange;
	line-height: 1.2;
	padding: 0;
	margin: 0;

	@include breakpoint(small only) {
		font-size: 2.8rem;
	}
}

h3 {
	font-family: $roboto_bold;
	font-size: 1.6rem;
	color: $orange;
	line-height: 1.5;
	padding: 0;
	margin: 0;
}

h4 {
	font-family: $roboto_regular;
	font-size: 1.8rem;
	color: $orange;
	line-height: 1.5;
	padding: 0;
	margin: 0;
}

h5 {
	font-family: $roboto_bold;
	font-size: 1.6rem;
	color: $orange;
	line-height: 1.5;
	padding: 0;
	margin: 0;
}

h6 {
	font-family: $roboto_bold;
	font-size: 1.6rem;
	color: $orange;
	line-height: 1.5;
	padding: 0;
	margin: 0;
}

p {
	font-family: $roboto_regular;
	font-size: 1.6rem;
	color: $black;
	line-height: 1.8;
	padding: 0;
	margin: 0;
}

span {
	font-family: $roboto_regular;
	font-size: 1.3rem;
	color: $black;
	line-height: 1;
	padding: 0;
	margin: 0;
}

ul {
	margin: 0;
	li {
		font-family: $roboto_regular;
		font-size: 1.6rem;
		color: $black;
		line-height: 1.4;
		list-style-type: none;
		padding: 0;
		margin: 0;
		a {
			font-family: inherit;
			font-size: inherit;
			color: inherit;
			line-height: inherit;
		}
	}
}

a {
	font-family: $roboto_regular;
	font-size: 1.8rem;
	color: $white;
	line-height: 1.4;
	padding: 0;
	margin: 0;
	@include transition(all 0.5s ease-out);
	&:hover {
		color: inherit;
		opacity: 0.4;
		@include transition(all 0.5s ease-out);
	}
}

label {
	display: block;
	font-family: $roboto_regular;
	font-size: 1.6rem;
	color: $black;
	line-height: 1.8;
	padding: 0;
	margin: 0 0 0.5rem;

	@include breakpoint(medium only) {
		font-size: 1.3rem;
	}

	@include breakpoint(small only) {
		font-size: 1.3rem;
	}
}

