.visuallyhidden {
	border: 0;
	width: 1px;
	height: 1px;
	margin: -1px;
	padding: 0;
	overflow: hidden;
	clip: rect(0 0 0 0);
	position: absolute;
}

.dropanchor {
	position: absolute;
	display: block;
	height: 100%;
	width: 100%;
	left: 0;
	top: 0;
	z-index: 1;
}

.grabbable {
	&:hover {
		cursor: move;
		/* fallback if grab cursor is unsupported */
		cursor: grab;
		cursor: -webkit-grab;
	}

	:active {
		cursor: grabbing;
		cursor: -webkit-grabbing;
	}
}

.mobile-view {
	display: none;
	@media screen and (max-width: 768px) {
		display: block;
	}
}

.desktop-view {
	display: block;
	@media screen and (max-width: 768px) {
		display: none;
	}
}