.contact {
	&__wrapper {
		position: absolute;
		height: 100%;
		width: 100%;
		top: 0;
		left: 0;
		@media screen and (max-width: 768px) {
			position: relative;
		}
	}
	&__container {
		position: relative;
		height: 100%;
		width: 100%;
		&--content {
			position: absolute;
			top: 50%;
			right: 0;
			text-align: right;
			@include transform(translateY(-50%));
			@media screen and (max-width: 768px) {
				display: none;
			}
			a {
				display: block;
				color: $white;
				margin-right: 3rem;
				font-family: $roboto_bold;
				font-size: 6rem;
				-webkit-text-stroke-width: 1px;
				-webkit-text-stroke-color: $orange;
				@include transition(all 0.5s ease-in);
				&:hover {
					color: $orange;
					opacity: 1;
				}
			}
			p {
				display: block;
				font-family: $roboto_bold;
				font-size: 5rem;
				color: $orange;
				line-height: 1;
				font-weight: 600;
			}
		}
	}
	&__content {
		position: absolute;
		top: 50%;
		left: 0;
		width: 100%;
		@include transform(translateY(-50%));
		@media screen and (max-width: 768px) {
			top: 40%;
		}
		&--mobile {
			margin: 5rem 0;
		}
	}
}



input:focus::-webkit-input-placeholder { color:transparent; }
input:focus:-moz-placeholder { color:transparent; } /* FF 4-18 */
input:focus::-moz-placeholder { color:transparent; } /* FF 19+ */
input:focus:-ms-input-placeholder { color:transparent; } /* IE 10+ */
textarea:focus::-webkit-input-placeholder { color:transparent; }
textarea:focus:-moz-placeholder { color:transparent; } /* FF 4-18 */
textarea:focus::-moz-placeholder { color:transparent; } /* FF 19+ */
textarea:focus:-ms-input-placeholder { color:transparent; } /* IE 10+ */

.cf7 {
	position: relative;
}
.wpcf7-text, .wpcf7-textarea {
	border-bottom: 1px solid $orange;
	color: $orange;
	font-family: $roboto_regular;
	font-size: 1.2rem;
	width: 100%;
	padding: 2rem 0.5rem 1rem;
	margin: 0 0 5px;
	@media screen and (max-width: 1366px) {
			padding: 1rem 0.5rem;
	}
	@media screen and (max-width: 768px) {
		font-size: 1rem;
	}
	&::-webkit-input-placeholder {
		color: $orange;
		font-size: 2rem;
		@media screen and (max-width: 1366px) {
			font-size: 1.5rem;
		}
		@media screen and (max-width: 768px) {
			font-size: 1rem;
		}
	}
	&::-moz-placeholder {
		color: $orange;
		font-size: 2rem;
		@media screen and (max-width: 1366px) {
			font-size: 1.5rem;
		}
		@media screen and (max-width: 768px) {
			font-size: 1rem;
		}
	}
	&:-ms-input-placeholder {
		color: $orange;
		font-size: 2rem;
		@media screen and (max-width: 1366px) {
			font-size: 1.5rem;
		}
		@media screen and (max-width: 768px) {
			font-size: 1rem;
		}
	}
	&:-moz-placeholder {
		color: $orange;
		font-size: 2rem;
		@media screen and (max-width: 1366px) {
			font-size: 1.5rem;
		}
		@media screen and (max-width: 768px) {
			font-size: 1rem;
		}
	}
}
.wpcf7-textarea {
	height: 10rem;
	@media screen and (max-width: 1366px) {
		height: 7rem;
	}
}
.wpcf7-submit {
	margin: 0 0 0 auto;
	display: block;
	background-color: transparent;
	color: $orange;
	font-family: $roboto_regular;
	cursor: pointer;
	font-size: 2rem;
	@media screen and (max-width: 1366px) {
		font-size: 1.5rem;
	}
	@media screen and (max-width: 768px) {
		font-size: 1rem;
	}
}
.screen-reader-response {
	display: none;
}
.wpcf7-not-valid-tip {
	font-family: Roboto,sans-serif;
	font-size: 12px;
	color: #ff0000;
	font-weight: 600;
	line-height: 1.5;
}
.wpcf7-response-output {
	text-align: center;
	font-family: Roboto,sans-serif;
	font-size: 12px;
	font-weight: 600;
	line-height: 1.5;
	&.wpcf7-validation-errors {
		color: #ff0000;
	}
	&.wpcf7-mail-sent-ng {
		color: #ff0000;
	}
	&.wpcf7-mail-sent-ok {
		color: #008000;
	}
}

.form__title {
	text-align: left;
	margin: 2rem 0;
	h1 {
		color: $orange;
		font-family: $roboto_bold;
		font-size: 2.6rem;
	}
}