// -------------------------------------------------

// Responsive Vertical Timeline - by CodyHouse.co

// -------------------------------------------------

:root {
	// colors
	@include defineColorHSL(--cd-color-1, 206, 21%, 24%); // Navy
	@include defineColorHSL(--cd-color-2, 205, 38%, 89%); // Blue
	@include defineColorHSL(--cd-color-3, 207, 10%, 55%); // Grey
	@include defineColorHSL(--cd-color-4, 111, 51%, 60%); // Green
	@include defineColorHSL(--cd-color-5, 356, 53%, 49%); // Red
	@include defineColorHSL(--cd-color-6, 47, 85%, 61%);  // Yellow

	// header
	--cd-header-height: 200px;

	// font
	--font-primary: 'Roboto', sans-serif;
	--font-secondary: 'Roboto', sans-serif;
}

@supports(--css: variables) {
	:root {
	@include breakpoint(md) {
		--cd-header-height:  300px;
	}
	}
}

.cd-main-header {
	height: var(--cd-header-height);
	background: var(--cd-color-1);
	color: var(--color-white);
	@include fontSmooth;

	h1 {
	font-family: var(--font-secondary);
	color: inherit;
	}
}

.cd-timeline {
	overflow: hidden;
	padding: var(--space-lg) 0;
	font-family: var(--font-primary);

	h2 {
	font-family: var(--font-secondary);
	font-weight: 700;
	}
}

.cd-timeline__container {
	position: relative;
	padding: 50px 0 100px;

	&::before { // this is the timeline vertical line
		content: '';
		position: absolute;
		top: 0;
		left: 18px;
		height: 100%;
		width: 4px;
		background: #000000;
		@media screen and (max-width: 768px) {
			left: 15px;
		}
	}

	@include breakpoint(md) {
	&::before {
		left: 50%;
		transform: translateX(-50%);
	}

	}
}

.cd-timeline__block {
	display: flex;
	position: relative;
	z-index: 1; // make sure content is above the timeline vertical line
	margin-bottom: var(--space-lg);

	&:last-child {
	margin-bottom: 0;
	}

	@include breakpoint(md) {
	&:nth-child(even) {
		flex-direction: row-reverse; // for even blocks -> lay out content from right to left
	}
	}
}

.cd-timeline__img {
	display: flex;
	justify-content: center;
	align-items: center;
	flex-shrink: 0;
	width: 30px;
	height: 30px;
	border-radius: 50%;

	img {
		display: none;
	}

	@include breakpoint(md) {
		width: 30px;
		height: 30px;
		order: 1; // flex order -> place the image after cd-timeline__content
		margin-left: calc(5% - 15px);
		will-change: transform;

		.cd-timeline__block:nth-child(even) & {
			margin-right: calc(5% - 15px);
		}
	}
}

.cd-timeline__img--picture {
	background-color: #000000;
}

.cd-timeline__content {
	flex-grow: 1; // expand element so that it takes up all the available space inside its parent
	position: relative;
	margin-left: var(--space-md);
	background: var(--color-white);
	border-radius: var(--radius);
	padding: var(--space-md);
	border: 1px solid #000000;

	&::before { // triangle next to content block
		content: '';
		position: absolute;
		top: 16px;
		right: 100%;
		@include triangle(left, 7px, #000000);
	}

	h2 {
		color:#F15922;
		font-size: 20px;
		font-family: 'Roboto', sans-serif;
		font-weight: 600;
	}

	p {
		font-size: 14px;
		font-family: 'Roboto', sans-serif;
		color: #000000;
	}

	@include breakpoint(md) {
	width: 45%;
	flex-grow: 0; // prevent element from growing inside its parent
	will-change: transform;
	margin: 0;
	font-size: 0.8em;
	--line-height-multiplier: 1.2;

	&::before { // triangle
		top: 24px;
	}

	.cd-timeline__block:nth-child(odd) &::before { // change triangle direction
		right: auto;
		left: 100%;
		@include triangle(right, 7px, #000000);
	}
	}
}

.cd-timeline__date {
	display: none;
}

@include breakpoint(md) { // animations
	.cd-timeline__img--hidden, .cd-timeline__content--hidden {
	visibility: hidden;
	}

	.cd-timeline__img--bounce-in {
	animation: cd-bounce-1 0.6s;
	}

	.cd-timeline__content--bounce-in {
	animation: cd-bounce-2 0.6s;
	}

	.cd-timeline__block:nth-child(even) .cd-timeline__content--bounce-in {
	animation-name: cd-bounce-2-inverse;
	}
}


@keyframes cd-bounce-1 {
	0% {
	opacity: 0;
	transform: scale(0.5);
	}
	60% {
	opacity: 1;
	transform: scale(1.2);
	}
	100% {
	transform: scale(1);
	}
}

@keyframes cd-bounce-2 {
	0% {
	opacity: 0;
	transform: translateX(-100px);
	}

	60% {
	opacity: 1;
	transform: translateX(20px);
	}

	100% {
	transform: translateX(0);
	}
}

@keyframes cd-bounce-2-inverse {
	0% {
	opacity: 0;
	transform: translateX(100px);
	}

	60% {
	opacity: 1;
	transform: translateX(-20px);
	}

	100% {
	transform: translateX(0);
	}
}