.projects__content {
	&--featured {
		height: 80vh;
		width: 100%;
		margin-top: 5rem;
		position: relative;

		@media only screen and (max-width: 1366px) {
			height: 70vh;
		}

		.projects__item {

			&--content {
				position: absolute;
				bottom: 3.5rem;
				left: 3.5rem;

				a {
					font-size: 3.2rem;
					font-family: $roboto_bold;
					color: $white;

					&:hover {
						color: $orange;
						opacity: 1;
						@include transition(color 0.5s ease);
					}

					@media only screen and (max-width: 1366px) {
						font-size: 2.8rem;
					}

				}

			}

		}

	}
	&--column {
		margin: 5rem 0 0;

		.projects__item {
			padding: 0 2rem 5rem;
			@media screen and (max-width: 768px) {
				padding: 0;
			}

			&:first-child {
				padding: 0 2rem 5rem 0;
				@media screen and (max-width: 768px) {
					padding: 0;
				}
			}

			&:nth-child(3) {
				padding: 0 0 5rem 2rem;
				@media screen and (max-width: 768px) {
					padding: 0 0;
				}
			}

			&:nth-child(4) {
				padding: 0 2rem 5rem 0;
				@media screen and (max-width: 768px) {
					padding: 0;
				}
			}

			&:nth-child(6) {
				padding: 0 0 5rem 2rem;
				@media screen and (max-width: 768px) {
					padding: 0;
				}
			}

			&:nth-child(7) {
				padding: 0 2rem 5rem 0;
				@media screen and (max-width: 768px) {
					padding: 0;
				}
			}

			&:last-child {
				padding: 0 0 5rem 2rem;
				@media screen and (max-width: 768px) {
					padding: 0;
				}
			}

			&--imgcontainer {
				position: relative;
				display: block;
				overflow: hidden;
				@include box-sizing(content-box);
			}

			&--image {
				height: 30rem;
				background-size: cover;
				background-position: center center;
				background-repeat: no-repeat;
				@include transition(all 3s ease);
				img {
					opacity: 0;
					visibility: hidden;
				}
				&:hover, &:focus {
					@include transform(scale(1.2));
				}
			}

			&--content {
				margin: 1rem 0;

				.title {
					margin: 1.5rem 0;
					font-size: 2rem;
					a {
						font-family: $roboto_bold;
						font-size: 2rem;
						color: $orange;
						line-height: 1.2;
						padding: 0;
						&:hover {
							opacity: 1;
							color: transparent;
							-webkit-text-stroke: 0.1rem $orange;
						}
					}
				}

				p {
					font-size: 1.4rem;
					margin-top: 1.5rem;
				}

			}

		}

	}

}

.title-content {
	&__container {
		margin: 95vh auto 5rem;
	}
	&__grid {
		margin: 0 auto;
	}
	&__title {
		margin: 5% 0 0;
		@media only screen and (max-width: 1366px) {
			margin: 7.5% 0 0;
		}
		h2 {
			text-transform: none;
			color: $white;
			font-size: 5rem;
			-webkit-text-stroke-width: 1px;
			-webkit-text-stroke-color: $orange;
			margin: 0;
		}
		@media screen and (max-width: 768px) {
			margin: 0 0 2rem;
			text-align: left;
		}
	}
	&__desc {
		padding-left: 1rem;
		.info {
			strong {
				font-family: $roboto_regular;
				color: $orange;
				font-size: 1.4rem;
			}
			p {
				font-family: $roboto_regular;
				color: #000000;
				font-size: 1.4rem;
			}
			margin: 0 0 2rem;
		}
		.desc {
			p {
				font-family: $roboto_regular;
				color: #000000;
				font-size: 1.4rem;
			}
		}
	}
}

.image-grid {
	&__container {
		position: relative;
		width: 80%;
		margin: 5rem auto;
		@media screen and (max-width: 768px) {
			width: calc(100% - 3rem);
			margin: 2rem auto;
		}
	}
}
.grid__images {
	@include flexbox();
	@include flex-flow(wrap);
	@include align-items(center);
	@include justify-content(center);
	position: relative;
	&.image--total-1 {
		.grid__images--container {
			&:first-child {
				@include flex(1 0 100%);
				width: 100%;
				padding: 1rem;
			}
		}
	}
	&.image--total-2 {
		.grid__images--container {
			&:first-child {
				@include flex(1 0 50%);
				width: 50%;
				padding: 1rem;
				@media screen and (max-width: 768px) {
					@include flex(1 0 100%);
					width: 100%;
				}
			}
			&:nth-child(2) {
				@include flex(1 0 50%);
				width: 50%;
				padding: 1rem;
				@media screen and (max-width: 768px) {
					@include flex(1 0 100%);
					width: 100%;
				}
			}
		}
	}
	&.image--total-3 {
		.grid__images--container {
			&:first-child {
				@include flex(1 0 33.33%);
				width: 33.33%;
				padding: 1rem;
				@media screen and (max-width: 768px) {
					@include flex(1 0 100%);
					width: 100%;
				}
			}
			&:nth-child(2) {
				@include flex(1 0 33.33%);
				width: 33.33%;
				padding: 1rem;
				@media screen and (max-width: 768px) {
					@include flex(1 0 100%);
					width: 100%;
				}
			}
			&:nth-child(3) {
				@include flex(1 0 33.33%);
				width: 33.33%;
				padding: 1rem;
				@media screen and (max-width: 768px) {
					@include flex(1 0 100%);
					width: 100%;
				}
			}
		}
	}
	&.image--total-4 {
		.grid__images--container {
			&:first-child {
				@include flex(1 0 25%);
				width: 25%;
				padding: 1rem;
				@media screen and (max-width: 768px) {
					@include flex(1 0 100%);
					width: 100%;
				}
			}
			&:nth-child(2) {
				@include flex(1 0 25%);
				width: 25%;
				padding: 1rem;
				@media screen and (max-width: 768px) {
					@include flex(1 0 100%);
					width: 100%;
				}
			}
			&:nth-child(3) {
				@include flex(1 0 25%);
				width: 25%;
				padding: 1rem;
				@media screen and (max-width: 768px) {
					@include flex(1 0 100%);
					width: 100%;
				}
			}
			&:nth-child(3) {
				@include flex(1 0 25%);
				width: 25%;
				padding: 1rem;
				@media screen and (max-width: 768px) {
					@include flex(1 0 100%);
					width: 100%;
				}
			}
		}
	}
	&.image--total-5 {
		.grid__images--container {
			&:first-child {
				@include flex(1 0 33.33%);
				width: 33.33%;
				padding: 1rem;
				@media screen and (max-width: 768px) {
					@include flex(1 0 100%);
					width: 100%;
				}
			}
			&:nth-child(2) {
				@include flex(1 0 33.33%);
				width: 33.33%;
				padding: 1rem;
				@media screen and (max-width: 768px) {
					@include flex(1 0 100%);
					width: 100%;
				}
			}
			&:nth-child(3) {
				@include flex(1 0 33.33%);
				width: 33.33%;
				padding: 1rem;
				@media screen and (max-width: 768px) {
					@include flex(1 0 100%);
					width: 100%;
				}
			}
			&:nth-child(4) {
				@include flex(1 0 50%);
				width: 50%;
				padding: 1rem;
				@media screen and (max-width: 768px) {
					@include flex(1 0 100%);
					width: 100%;
				}
			}
			&:nth-child(5) {
				@include flex(1 0 50%);
				width: 50%;
				padding: 1rem;
				@media screen and (max-width: 768px) {
					@include flex(1 0 100%);
					width: 100%;
				}
			}
		}
	}
}

.full-content__container {
	position: relative;
	width: 70%;
	margin: 5rem auto;
	@media screen and (max-width: 768px) {
		width: calc(100% - 3rem);
		margin: 2rem auto;
	}
}
.full__content {
	p {
		font-family: $roboto_regular;
		color: #000000;
		font-size: 1.4rem;
		margin: 0 0 1.5rem;
		@media screen and (max-width: 768px) {
			font-size: 1.2rem;
		}
	}
}

.full-video {
	&__container {
		position: relative;
		padding: 15% 0 0;
		@media only screen and (max-width: 1366px) {
			padding: 13% 0 0;
		}
		@media screen and (max-width: 768px) {
			padding: 0 0;
		}
	}
	&__bg {
		background-color: $orange;
	}
	&__content {
		width: 70%;
		margin: 5rem auto 0;
		@media screen and (max-width: 768px) {
			width: 100%;
		}
	}
}
.full__video {
	&--video {
		width: 65%;
		position: absolute;
		top: 0;
		left: 50%;
		@include transform(translateX(-50%));
		@media screen and (max-width: 768px) {
			top: 2rem;
			left: 1.5rem;
			width: calc(100% - 3rem);
			@include transform(none);
		}
		video {
			width: 100%;
			height: auto;
		}
	}
	&--content {
		padding: 30% 0 5rem;
		width: 70%;
		margin: 0 auto;
		@include flexbox();
		@include flex-flow(wrap);
		@include align-items(start);
		@include justify-content(center);
		@media only screen and (max-width: 1366px) {
			padding: 32% 0 5rem;
		}
		@media screen and (max-width: 768px) {
			padding: 25rem 0 2rem;
			width: 90%;
		}
		.video__content--title {
			@include flex(1 0 40%);
			width: 40%;
			text-align: center;
			@media screen and (max-width: 768px) {
				@include flex(1 0 100%);
				width: 100%;
				text-align: left;
			}
			h2 {
				text-transform: none;
				font-family: $roboto_bold;
				color: transparent;
				font-size: 5rem;
				-webkit-text-stroke-width: 1px;
				-webkit-text-stroke-color: $white;
				margin: 0;
			}
		}
		.video__content--desc {
			@include flex(1 0 60%);
			width: 60%;
			@media screen and (max-width: 768px) {
				@include flex(1 0 100%);
				width: 100%;
			}
			p {
				font-family: $roboto_regular;
				color: $white;
				font-size: 1.2rem;
			}
		}
	}
}

.photo-content {
	&__container {
		position: relative;
	}
	&__desc {
		position: relative;
	}
	&__title {
		text-align: right;
		@media screen and (max-width: 768px) {
			text-align: left;
		}
		h2 {
			text-transform: none;
			color: $white;
			font-size: 5rem;
			-webkit-text-stroke-width: 1px;
			-webkit-text-stroke-color: $orange;
			margin: 0;
			@media screen and (max-width: 768px) {
				font-size: 4rem;
			}
		}
		span {
			font-family: $roboto_bold;
			color: $orange;
			font-size: 1.8rem;
			text-align: right;
		}
	}
	&__con {
		text-align: left;
		.desc {
			margin-top: 2rem;
		}
		.info {
			margin-top: 3rem;
			@media screen and (max-width: 768px) {
				margin-top: 1.5rem;
			}
			p {
				font-size: 1.2rem;
			}
		}
		@media screen and (max-width: 768px) {
			margin-bottom: 2rem;
		}
	}
	&__image {
		img {
			width: 100%;
			height: auto;
		}
	}

}

.tall-photo {
	&__container {
		padding: 7rem 0 0;
	}
	&__image {
		img {
			width: 100%;
			height: auto;
		}
	}
}

.full-photo {
	&__container {
		background-color: $orange;
		@media screen and (max-width: 768px) {
			margin-bottom: -0.2rem;
		}
	}
	&__image {
		.image__grid {
			text-align: center;
			img {
				display: block;
				width: 60%;
				height: auto;
				margin: -22% auto 10%;
				@media screen and (max-width: 768px) {
					width: 70%;
				}
			}
		}
	}
}

