.privacy__content {
	margin: 5rem auto;
	ul {
		padding: 0 0 0 3rem;
		li {
			list-style: disc;
		}
	}
	h3 {
		margin-top: 3rem;
	}
	h2 {
		margin-top: 3rem;
	}
	a {
		color: $black;
		font-size: 1.6rem;
		&:hover {
			text-decoration: underline;
			opacity: 1;
			color: $orange;
		}
	}
	&--image {
		position: relative;
		height: 100%;
		width: 100%;
		background-size: contain;
		background-position: center center;
		background-repeat: no-repeat;
		h1 {
			font-family: $roboto_bold;
			color: $white;
			position: absolute;
			top: 50%;
			left: 50%;
			@include transform(translate(-50%, -50%));
		}
		img {
			visibility: hidden;
			opacity: 0;
		}
	}
}

.privacy__banner {
	margin: 5rem auto 0;
}